*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
h1,h2, h3, h4, p{
  margin: 0;
  padding: 0;
}
li{
  list-style: none;
}
ul {
   display: flex;
}

body {
  margin: 0;
  padding: 0;
  background-color: #F6F6F6;
  background-color: var(--bgColor);
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif, 'Anton', 'Bebas Neue','Oswald';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* color variables */
:root{
  --bgColor : #F6F6F6;
  --blueColor : #006491;
  --darkBlue : #005177;
  --linkColor : #E31837;
  --footerColor : #DBDBDB;
  --darkText : #444444;

}
button{
  color: white;
  font-family: 'Bebas Neue';
  background-color: #E31837;
  border-radius: 4px;
  border: none;
  outline: none;
  padding: 12px 55px;
  letter-spacing: 1px;
  font-size: 20px;
  cursor: pointer;
  transition: 0.4s;
}
button:hover{
background-color:#ac0921;
}
.Link {
  color: white;
  text-decoration: none;
  font-family: 'Bebas Neue';
  letter-spacing: 1px;
  font-size: 16.5px;
}
.material{
  color: white;
  font-size: 35px !important;
}
/* ==HEADER LAYOUT================================ */
.header__wrapper{
    width: 100%;
    background-color: var(--blueColor);
    box-shadow: 0px 2px 10px #4e4d4d;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 2;
}

.header {
    display: flex;
    align-items: center;
    justify-content: space-between;

    max-width: 950px;
    max-height: 75px;
    margin: 0 auto;
    padding: 0 10px;
    height: 75px;
    background-color: var(--blueColor);
    overflow: hidden;    
}
.hamburgerMenu{
    display: none !important;
}
.header__sub{
    width: 100%;
    height: 35px;
    background-color: var(--bgColor);
}
.header__subMenu {
max-width: 950px;
margin: 0 auto;
}

/* ==HEADER MENU SECTION================================ */

.headerLogo {
    width: 44px;
    object-fit: contain;
    margin-right: 7px;
}
.header__menu{
    display: flex;
    align-items: center;
    flex: 1 1;
}
.headerLink {
    padding: 100px 10px;
    transition: 0.4s;
  }
  .headerLink:hover{
    background-color: var(--darkBlue);
    }

/* SIGNIN SECTION================================== */

.header__signin{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--darkBlue);
    padding: 100px 10px;
}
.headerAvater {
   margin: 0 15px;
}
.header__signin > p{
    padding: 0 40px;   
}
.headerSpan {
    font-size: 14px;
    color: white;
    font-family: 'Bebas Neue';
    letter-spacing: 1px;
}

/* CART SECTION===================================== */

.header__cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 20px 0 30px;
}

/* sub menu section==================================== */

  .submenu {
    max-width: 950px;
    max-height: 36px;
    margin: 0 auto;
    padding-left: 50px;
    font-family: 'Bebas Neue';
    font-weight: 500;
    z-index: 2;
  }

  .submenu__li {
    padding: 8px 10px;
    font-size: 15px;
    color: rgb(4, 98, 141);
    letter-spacing: 1px;
  }
  .submenu__link {
    text-decoration: none;
  }
  /* guest */
  .user {
      font-family: 'Roboto';
      font-weight: 400;
      text-transform: capitalize;
  }

  /* media queries */
  @media screen and (max-width : 800px){
      .header {
        display: flex;
        align-items: center;
        justify-content: space-between;
    
        width: 100%;
        max-height: 50px;
        margin: 0 auto;
        background-color: var(--blueColor);
        overflow: hidden;  
      }
      .header__menu > ul {
          display: none;
      }
      .headerLogo {
          width: 40px;
      }
      .header__signin{
        display: none;
    }
    .header__cart {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin: 0;
    }
    .cartResponsive{
        margin-right: 10px;
    }
    .material {
        
        font-size: 30px !important;
    }
    .headerSpan{
        font-weight: 200;
        font-size: 12px;
    }
    .hams {
    flex: 1 1;
    }
    .hamburgerMenu{
        display: block !important;
        color: white;
        font-size: 40px !important;
    }
    
  }
.signin__wrapper {
  background-color: rgba(255, 255, 255, 0.76);
  position: fixed;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
}

.signin {
  max-width: 550px;
  position: relative;
  margin: 50px auto 0 auto;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid rgb(190, 189, 189);
  background-color: white;
}
.signin__header {
  height: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  border-radius: 4px 4px 0 0;
}
.signin__header > span {
  font-family: 'Bebas Neue';
  font-size: 43px;
  font-weight: 500;
  letter-spacing: 1.5px;
  padding-left: 5px;
  color: var(--darkText);
  flex: 1 1;
  
}
.signin__header > button {
  border: none;
  background: none;
  outline: none;
  padding: 0;
}
.header__closeIcon {
  color: black;
  cursor: pointer;
}
.signin__body > form {
  color: var(--darkText);
  max-height: 400px;
}
.signin__body > form {
  display: flex;
  flex-direction: column;
}
.signin__button {
  display: flex;
  justify-content: space-between;
}
.signin__button > button {
 margin-top: 20px;
}
.signin__button2 {
  margin-left: 10px;
  padding: 7px 20px;
  flex: 1 1;
}
.signin__button1 {
  background-color: var(--blueColor);
  margin-right: 10px;
  padding: 7px 20px;
  flex: 1 1;
}
.signin__button1:hover {
  background-color: var(--darkBlue);
}

.signin__body > form > p,
.signin__body > form > label {
  font-family: sans-serif;
  font-size: 11px;
  font-weight: bold;
}
.signin__body > form > input {
  margin-bottom: 10px;
  border: 1px solid grey;
  padding: 5px;
  outline: none;
}
.resetPassword {
  color: var(--blueColor);
  float: right;
  text-decoration: underline;
}
.resetPassword:hover {
  color: var(--linkColor);
  text-decoration: underline;
}
.securedOrder {
  padding-top: 10px;
  color: #b1b0b0;
  float: right;
  font-size: 9px;
  padding-left: 275px;
}
.signupLink{
  color: var(--blueColor);
  font-size: 14px;
  text-decoration: none;
}
.signupLink:hover{
  color: var(--linkColor);
}
.cart {
    position: fixed;
    top: 10%;
    left: 70%;
    transform: translateX(-50%);
    box-shadow: 1px 3px 10px rgb(80, 80, 80);
    width: 500px;
    min-height: 200px;
    background-color: white;
    display: flex;
    flex-direction: column;
  }
  .cart__header {
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid var(--blueColor);
    padding: 10px;
  }
  .cart__header > h1 {
    font-family: "Bebas Neue";
    font-weight: 500;
    color: var(--blueColor);
    font-size: 25px;
    margin-bottom: 0;
  }
  .cart__header > button {
    padding: 0;
    background: none;
    border: none;
    outline: none;
  }
  .cart__closebtn:hover {
    background-color: rgb(197, 194, 194);
    transition: 0.5s;
    border-radius: 5px;
  }
  .cart__closebtn {
    color: var(--blueColor);
    font-size: 25px !important;
  }
  

.individual__main {
    display: flex;
    flex-direction: column;
    padding: 10px;
}
.individual__item > h1 {
    color: var(--blueColor);
    font-size: 15px;
    font-weight: 500;
    flex: 1 1;
    margin-left: 15px;
}
.individual__item{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    border-top: 1px solid rgb(189, 186, 186);
    padding-top: 10px;
}
.individual__item > img {
    width: 13%;
    border-radius: 5px;
}
.individual__item > p {
font-size: 13px;
margin-right: 20px;
color: rgb(71, 71, 71);
}

/* editable section--------------------*/
.individual__editable{
    display: flex;
    align-items: baseline;
    justify-content: flex-start;
    margin-top: 15px;
    margin-bottom: -10px;
}
.individual__editable > label{
    font-size: 13px;
}
.individual__editable > label > select{
width: 45px;
height: 25px;
margin-right: 20px;

}
